import React from 'react';
import {
  ServerRenderContext,
  createServerRenderContextV2,
  ClientRenderContextProvider,
} from '@hubspot/cms-components';

const contextValue = createServerRenderContextV2('http://testUrl.com', {
  environmentConfig: null,
});

export default {
  parameters: {
    actions: { argTypesRegex: '^on[A-Z].*' },
  },
  decorators: [
    Story => (
      <ClientRenderContextProvider pageUrl="http://test.com" islands={[]}>
        <ServerRenderContext.Provider value={contextValue}>
          <Story />
        </ServerRenderContext.Provider>
      </ClientRenderContextProvider>
    ),
  ],
};
